* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.search-form {
  height: 50px;
  background-color: #3658be;
  justify-content: center;
  padding: 10px;
  display: flex;
  box-shadow: 1px 1px 3px 2px rgba(0, 0, 0, .2);
}

.search-form__body {
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.search-form__input {
  height: 30px;
  width: 300px;
  border: 2px solid rgba(0, 0, 0, 0);
  outline: none;
  padding: 5px;
  display: block;
}

.search-form__body:focus-within {
  border-radius: 5px;
  outline: 2px solid #3c9cd4;
}

.search-form__btn {
  height: 100%;
  width: 30px;
  background-image: url("search-zoom.a76b9de5.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50%;
  border: none;
  position: absolute;
  top: 0;
  right: 0;
}

